import React from 'react'
import { Link } from 'gatsby'
import NavContainer from 'theme/containers/NavContainer'

// '/search/','/blog/'

const PATHS = ['/',  '/about/','/tags/']
const getLinkName = locationSlug => (locationSlug.length === 1 ? 'Home' : locationSlug.split('/')[1])


const Li = ({ location }) => (
  <li>
    <Link to={location}>{getLinkName(location)}</Link>
  </li>
)


export default () => (
  <div>
    <NavContainer>
      <ul>
        {PATHS.map((path, i) => <Li key={i} location={path} />)}
      </ul>
    </NavContainer>
  </div>
)
